import './Face.css';

function Face(props) {
  return (
    <div className="Face">
      <header className="App-header">
            <div className="Lead">
                {props.start}
                <div className="My-Name">
                    {props.name}
                </div>
                .
            </div>
            {props.end}
      </header>
    </div>
  );
}


export default Face;
